@import url('https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,200;0,300;0,400;0,500;1,200;1,300;1,400;1,500&display=swap');
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  position: absolute;
  left: 0;
  top: 0;
  min-width: 4vmin;
  min-height: 4vmin;
  padding-left: 2vmin;
  padding-right: 2vmin;
  padding-top: 2vmin;
  padding-bottom: 2vmin;
  font-size: calc(10px + 2vmin);
  color: white;
  z-index: 11;
}

.App-link {
  color: #61dafb;
}

.App-main {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: left;
  font-size: calc(10px + 2vmin);
  color: white;
}
.App-background {
  width: 100%;
  height: 100%;
  background-position: center center;
  background-repeat: no-repeat;
  overflow: hidden; 
  z-index: 1;
}
.App-intro {
  position: absolute;
  left: 50%;
  top: 25%;
  width: 40%;
  min-height:  100vh;
  padding-right: 2vmin;
  font-size: calc(10px + 2vmin);
  color: white;
  z-index: 3;
}
.App-hi{
  font-size: 8vmin;
  text-align: left;
  margin-bottom: 4vmin;
  font-family: 'Montserrat';
}
.App-myname{
  font-size: 4vmin ;
  text-align: left;
  margin-bottom: 4vmin;
  font-family: 'Montserrat';
  font-weight: 200;
  font-style: italic;
}
.App-mybio{
  text-align: left;
}
.App-navigator {
  background-color: #001e5a80;
  position:absolute;
  top: 0;
  left: 0;
  z-index: -1;
  min-height: 100vh;
  min-width: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-footer {
  background-color: #3c7bf8;
  min-height: 20vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
